// Ad rotator
.rotator {
    position: relative;
    .g-col {
        display: none;
        &.show {
            display: inline-block;
        }
    }
    .skipper {
        position: absolute;
        top: -5px;
        right: 1px;
        i {
            cursor: pointer;
        }
    }
}

